// src/pages/Stats/Stats.js
import React from "react";
import { Dropdown } from "primereact/dropdown";
import { Panel } from "primereact/panel";
import { Card } from "primereact/card";
import { DataTable } from "primereact/datatable";
import { Column } from "primereact/column";
import { Chart } from "primereact/chart"; // Chart  import
import "./Stats.scss";
import { useStats } from "../../hooks/useStats";
import { formatCurrency } from "../../util/formatCurrency";
import { useNavigate } from "react-router-dom";

function Stats() {
  const {
    topSales,
    avgSalesInfo,
    lowestFranchiseFeeStores,
    newOpeningsCount,
    region,
    setRegion,
    industry,
    setIndustry,
    limit,
    setLimit,
  } = useStats();

  const regionOptions = [
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
  ];

  const industryOptions = [
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: " ", value: " " },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: " ", value: " " },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "", value: "" },
    { label: "/", value: "/" },
    { label: " ( )", value: " ( )" },
  ];

  const limitOptions = [
    { label: 10, value: 10 },
    { label: 20, value: 20 },
    { label: 100, value: 100 },
  ];

  const navigate = useNavigate();
  // "  "    
  const avgSalesData = {
    labels: avgSalesInfo.map((info) => info.industry),
    datasets: [
      {
        label: " ",
        data: avgSalesInfo.map((info) => info.industry_avg_monthly_sales),
        backgroundColor: "#42A5F5", //  
      },
    ],
  };

  // "   "    
  const newOpeningsData = {
    labels: newOpeningsCount.map((opening) => opening.industry),
    datasets: [
      {
        label: "  ",
        data: newOpeningsCount.map((opening) => opening.new_openings_count),
        backgroundColor: "#66BB6A", //  
      },
    ],
  };
  const combinedData = {
    labels: avgSalesInfo.map((info) => info.industry),
    datasets: [
      {
        label: " ",
        data: avgSalesInfo.map((info) => info.industry_avg_monthly_sales),
        backgroundColor: "#42A5F5", //  
        yAxisID: "y-axis-sales", //   Y 
      },
      {
        label: "  ",
        data: newOpeningsCount.map((opening) => opening.new_openings_count),
        backgroundColor: "#66BB6A", //  
        yAxisID: "y-axis-openings", //   Y 
      },
    ],
  };

  //     
  const handleStoreNameClick = (storeName) => {
    navigate(`/search?storeName=${encodeURIComponent(storeName)}`);
  };
  return (
    <div className="stats-page">
      <div className="stats-title">
        <span> </span>
        <div className="stats-title-select">
          <Dropdown
            value={region}
            options={regionOptions}
            onChange={(e) => setRegion(e.value)}
            placeholder=" "
            className="stats-dropdown"
            virtualScrollerOptions={{ itemSize: 30 }}
          />

          <Dropdown
            value={industry}
            options={industryOptions}
            onChange={(e) => setIndustry(e.value)}
            placeholder=" "
            className="stats-dropdown"
            virtualScrollerOptions={{ itemSize: 30 }}
          />
          <Dropdown
            value={limit}
            options={limitOptions}
            onChange={(e) => setLimit(e.value)}
            placeholder="  "
            className="stats-dropdown"
            virtualScrollerOptions={{ itemSize: 30 }}
          />
        </div>
      </div>
      <div className="stats-content1">
        <div className="content1-box">
          <div class="content1-box1-section">
            <div>
              <span className="title">   </span>
            </div>
            <div>
              <span className="value">21112</span>
            </div>
          </div>
          <div class="content1-box2-section">
            <div>
              <span className="title">   </span>
            </div>
            <div>
              <span className="value">21112</span>
            </div>
          </div>
        </div>
        <div className="content1-graph">
          {/*     */}
          <Panel
            header="      "
            toggleable
            className="stats-panel"
          >
            <Chart
              type="bar"
              data={combinedData}
              options={{ maintainAspectRatio: false }}
            />
          </Panel>
        </div>
      </div>

      <div className="stats-content2">
        {/*     */}
        <div className="content2-table1">
          <Panel header="  " toggleable className="stats-panel">
            <DataTable
              value={topSales}
              paginator
              rows={5}
              className="p-datatable-custom"
            >
              <Column
                field="store_name"
                header=""
                body={(rowData) => (
                  <span
                    style={{ cursor: "pointer", color: "#007bff" }}
                    onClick={() => handleStoreNameClick(rowData.store_name)}
                  >
                    {rowData.store_name}
                  </span>
                )}
              />
              <Column
                field="avg_monthly_sales"
                header=" "
                body={(rowData) => formatCurrency(rowData.avg_monthly_sales)}
              />
              <Column
                field="franchise_fee"
                header=" "
                body={(rowData) => formatCurrency(rowData.franchise_fee)}
              />
              <Column field="store_count" header=" " />
            </DataTable>
          </Panel>
        </div>
        <div className="content2-table2">
          {/*      */}
          <Panel
            header="   "
            toggleable
            className="stats-panel"
          >
            <DataTable
              value={lowestFranchiseFeeStores}
              paginator
              rows={5}
              className="p-datatable-custom"
            >
              <Column
                field="store_name"
                header=""
                body={(rowData) => (
                  <span
                    style={{ cursor: "pointer", color: "#007bff" }}
                    onClick={() => handleStoreNameClick(rowData.store_name)}
                  >
                    {rowData.store_name}
                  </span>
                )}
              />
              <Column
                field="avg_monthly_sales"
                header=" "
                body={(rowData) => formatCurrency(rowData.avg_monthly_sales)}
              />
              <Column
                field="franchise_fee"
                header=" "
                body={(rowData) => formatCurrency(rowData.franchise_fee)}
              />
              <Column field="industry" header="" />
            </DataTable>
          </Panel>
        </div>
      </div>

      {/*     */}
      {/* <Panel header="   " toggleable className="stats-panel">
        <div className="card-grid">
          {newOpeningsCount.map((opening, index) => (
            <Card key={index} className="stat-card">
              <p>{opening.industry}</p>
              <p> : {opening.new_openings_count}</p>
            </Card>
          ))}
        </div>
      </Panel> */}

      {/*      */}
      {/* <Panel
        header="    "
        toggleable
        className="stats-panel"
      >
        <Chart type="bar" data={newOpeningsData} />
      </Panel> */}
    </div>
  );
}

export default Stats;
