import React from "react";
import "./CostInfo.scss";
import MapWithStoreCounts from "./MapWithStoreCounts"; // MapWithStoreCounts  

function CostInfo({ costInfo }) {
  console.log(costInfo);
  return (
    <div className="cost-info">
      <MapWithStoreCounts costInfo={costInfo} />
    </div>
  );
}

export default CostInfo;
