// src/util/formatCurrency.js

export function formatCurrency(value) {
  if (value >= 100000000) {
    //    
    const billions = Math.floor(value / 100000000); //  
    const remaining = Math.floor((value % 100000000) / 10000); //   
    return remaining > 0 ? `${billions} ${remaining}` : `${billions}`;
  } else if (value >= 10000) {
    return `${Math.floor(value / 10000)}`; //   
  } else {
    return `${value}`; //      
  }
}
