import React from "react";
import { Chart } from "primereact/chart";

function SalesInfoChart({ labels, data, title }) {
  //       
  const maxDataValue = Math.max(...data);
  const backgroundColors = data.map(
    (value) => (value === maxDataValue ? "#d9534f" : "#337ab7") //  ,  
  );

  const chartData = {
    labels: labels, // x 
    datasets: [
      {
        label: title,
        backgroundColor: backgroundColors, //    
        borderColor: "#1E88E5",
        borderWidth: 1,
        data: data, //  
      },
    ],
  };

  const options = {
    responsive: true,
    maintainAspectRatio: false,
    scales: {
      x: {
        beginAtZero: true,
        ticks: {
          font: {
            family: "Maplestory Bold",
          },
        },
      },
      y: {
        beginAtZero: true,
        ticks: {
          font: {
            family: "Maplestory Bold",
          },
        },
      },
    },
    plugins: {
      legend: {
        display: false, // ( ) 
      },
    },
  };

  return (
    <div>
      <Chart type="bar" data={chartData} options={options} />
    </div>
  );
}

export default SalesInfoChart;
