// src/hooks/useSearch.js
import { useState, useEffect, useRef } from "react";
import {
  getCompanyNameList,
  getCompanyBasicInfo,
  getCompanySalesInfo,
  getCompanyRegionTotalCount,
} from "../service/searchApi";

export function useSearch() {
  const [searchType, setSearchType] = useState("brand");
  const [searchWord, setSearchWord] = useState("");
  const [companyNo, setCompanyNo] = useState("");
  const [suggestions, setSuggestions] = useState([]);
  const [showSuggestions, setShowSuggestions] = useState(false);
  const [highlightIndex, setHighlightIndex] = useState(-1);
  const suggestionRefs = useRef([]);
  const [selectedTab, setSelectedTab] = useState("");
  const [basicInfo, setBasicInfo] = useState();
  const [salesInfo, setSalesInfo] = useState();
  const [costInfo, setCostInfo] = useState({ data: [] });
  const [salesInfoRegion, setSaleInfoRegion] = useState("");

  //       
  const handleInputChange = async (value) => {
    setSearchWord(value);
    setHighlightIndex(-1);

    if (value.trim() === "") {
      //    
      setSuggestions([]);
      setShowSuggestions(false);
      return;
    }

    try {
      const data = await getCompanyNameList(searchType, value);
      setSuggestions(data.data || []);
      setShowSuggestions(data.data && data.data.length > 0); //    
    } catch (error) {
      console.error("   :", error);
      setSuggestions([]);
      setShowSuggestions(false);
    }
  };

  //       
  const handleSuggestionClick = (suggestion) => {
    setSearchWord(suggestion.name);
    setCompanyNo(suggestion.fir_mst_sn);
    setShowSuggestions(false); //   
    setSuggestions([]); //   
    setSelectedTab("brandInfo");
    setHighlightIndex(-1);
    fetchTabData("brandInfo", suggestion.fir_mst_sn);
  };

  // API  
  const fetchTabData = async (tab, companyNo) => {
    try {
      let data;
      if (tab === "sales") {
        data = await getCompanySalesInfo(companyNo, salesInfoRegion);
        setSalesInfo(data);
      } else if (tab === "cost") {
        data = await getCompanyRegionTotalCount(companyNo);
        setCostInfo(data);
      } else if (tab === "brandInfo") {
        data = await getCompanyBasicInfo(companyNo);
        setBasicInfo(data);
      }
    } catch (error) {
      console.error(`API  (${tab}):`, error);
    }
  };

  const handleTabChange = (tab) => {
    setSelectedTab(tab);
    if (companyNo) {
      fetchTabData(tab, companyNo);
    }
  };

  //      
  const handleSalesInfoRegionChange = async (region) => {
    setSaleInfoRegion(region);
    if (companyNo) {
      try {
        const data = await getCompanySalesInfo(companyNo, region);
        setSalesInfo(data);
      } catch (error) {
        console.error("     :", error);
      }
    }
  };

  const handleKeyDown = (e) => {
    if (showSuggestions) {
      if (e.key === "ArrowDown") {
        setHighlightIndex((prevIndex) =>
          prevIndex < suggestions.length - 1 ? prevIndex + 1 : 0
        );
      } else if (e.key === "ArrowUp") {
        setHighlightIndex((prevIndex) =>
          prevIndex > 0 ? prevIndex - 1 : suggestions.length - 1
        );
      } else if (e.key === "Enter" && highlightIndex >= 0) {
        handleSuggestionClick(suggestions[highlightIndex]);
      }
    }
  };

  useEffect(() => {
    if (highlightIndex >= 0 && suggestionRefs.current[highlightIndex]) {
      suggestionRefs.current[highlightIndex].scrollIntoView({
        behavior: "smooth",
        block: "nearest",
      });
    }
  }, [highlightIndex]);

  return {
    searchType,
    searchWord,
    suggestions,
    companyNo,
    showSuggestions,
    selectedTab,
    setSelectedTab,
    setSearchType,
    setSearchWord,
    highlightIndex,
    suggestionRefs,
    handleInputChange,
    handleSuggestionClick,
    handleKeyDown,
    handleTabChange,
    basicInfo,
    setBasicInfo,
    salesInfoRegion,
    salesInfo,
    handleSalesInfoRegionChange,
    costInfo,
    setCostInfo,
    setSuggestions,
    setShowSuggestions,
  };
}
